<template>
  <v-card>
    <v-card-title>
      <v-btn @click="back" icon medium class="white--text mr-1" v-if="multiStepViewConfig[currentView].hasBack">
        <v-icon medium dark> mdi-arrow-left </v-icon>
      </v-btn>
      <span class="sub-headline"> Slave Apps | {{ multiStepViewConfig[currentView].title }} </span>
    </v-card-title>
    <v-card-text>
      <v-stepper v-model="currentView">
        <v-stepper-header>
          <v-stepper-step :complete="currentView > 1" step="1"> 1 </v-stepper-step>

          <v-divider></v-divider>
          <v-stepper-step :complete="currentView > 2" step="2"> 2 </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step :complete="currentView > 3" step="3"> 3 </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step step="4"> 4 </v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content step="1" class="pa-0">
            <v-alert class="mb-0" v-if="!hasSelectedApps" dense type="info">
              Please select apps from the grid to bulk download.
            </v-alert>
            <v-list flat subheader dense class="text-left">
              <v-subheader>Selected Apps</v-subheader>
              <template v-for="(app, i) in apps">
                <v-list-item v-if="app.isHeader" :key="`selected_apps_header-${i}`">
                  <v-list-item-content>
                    <v-list-item-title v-text="app.name"></v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item class="pl-10" v-if="app.isListItem" :key="`selected_apps-${i}`">
                  <v-list-item-content>
                    <v-list-item-title v-text="app.slaveDeviceName"></v-list-item-title>
                    <v-list-item-subtitle v-text="app.version"></v-list-item-subtitle>
                  </v-list-item-content>
                  <v-list-item-icon>
                    <app-status-chip :state="app.state"></app-status-chip>
                  </v-list-item-icon>
                </v-list-item>
              </template>
            </v-list>
          </v-stepper-content>
          <v-stepper-content step="2">
            <v-form v-model="versionForm" ref="versionForm" lazy-validation>
              <v-row dense class="text-left">
                <template v-for="(app, i) in apps">
                  <v-col :key="`version-form-header-${renderReleaseCount}-${i}`" cols="12" v-if="app.isHeader">
                    <span class="caption font-weight-bold">{{ app.name }}</span>
                  </v-col>
                  <v-col :key="`version-form-field-${renderReleaseCount}-${i}`" cols="4" v-if="app.isListItem">
                    <text-field-display
                      class="pl-5"
                      label="Slave Name"
                      :value="app.slaveDeviceName"
                    ></text-field-display>
                  </v-col>
                  <v-col :key="`version-form-select-${renderReleaseCount}-${i}`" cols="12" v-if="app.isVersionSelect">
                    <v-combobox
                      class="pl-5"
                      :items="appVersions[app.name]"
                      label="Select Release Version"
                      hideSelected
                      clearable
                      v-model="app.model"
                      item-text="name"
                      item-value="tag_name"
                      :rules="versionRules"
                    >
                    </v-combobox>
                  </v-col>
                </template>
              </v-row>
            </v-form>
          </v-stepper-content>
          <v-stepper-content step="3" class="pa-0">
            <div>
              <v-alert class="mb-0" v-if="currentDownloadState === 'DOWNLOAD_IN_PROGRESS'" dense type="info">
                Downloading App(s) <strong>Please Wait!</strong>
              </v-alert>
              <v-alert class="mb-0" v-if="currentDownloadState === 'DOWNLOAD_COMPLETE'" dense type="success">
                Downloading Completed
              </v-alert>
            </div>
            <v-list flat subheader dense class="text-left" v-if="currentDownloadState === 'DOWNLOAD_COMPLETE'">
              <v-subheader>App Download Statuses</v-subheader>
              <template v-for="(app, i) in downloadStates">
                <v-row dense v-if="app.isHeader" :key="`download-statuses-header-${i}`">
                  <v-col cols="12" class="pl-3">
                    <span class="caption font-weight-bold">{{ app.name }}</span>
                  </v-col>
                </v-row>
                <v-list-item class="pl-5" v-if="app.isListItem" :key="`download-statuses-${i}`">
                  <v-list-item-content>
                    <v-list-item-title v-text="app.slaveDeviceName"></v-list-item-title>
                    <v-list-item-subtitle v-text="app.version"></v-list-item-subtitle>
                  </v-list-item-content>
                  <v-list-item-icon>
                    <div v-if="!app.isDownloading">
                      <span class="font-weight-medium">
                        <v-chip outlined color="yellow">Downloading</v-chip>
                      </span>
                    </div>
                    <div v-else-if="app.hasError">
                      <span class="font-weight-medium">
                        <v-chip outlined color="red">Failed</v-chip>
                      </span>
                    </div>
                    <div v-else-if="app.isDownloading && !app.hasError">
                      <span class="font-weight-medium">
                        <v-chip outlined color="green">Downloaded</v-chip>
                      </span>
                    </div>
                  </v-list-item-icon>
                </v-list-item>
              </template>
            </v-list>
          </v-stepper-content>
          <v-stepper-content step="4" class="pa-0">
            <v-list flat subheader dense class="text-left" v-if="currentDownloadState === 'DOWNLOAD_COMPLETE'">
              <v-list-item-group v-if="installationSuccessApps.length > 0">
                <v-subheader>Installation Completed Apps</v-subheader>
                <template v-for="(successApps, i) in installationSuccessApps">
                  <v-row v-if="successApps.isHeader" :key="`install-complete-header-${i}`">
                    <v-col cols="12">
                      <span class="caption font-weight-bold">{{ successApps.name }}</span>
                    </v-col>
                  </v-row>
                  <v-list-item v-if="successApps.isListItem" :key="`install-complete-${i}`">
                    <v-list-item-content>
                      <v-list-item-title v-text="successApps.slaveDeviceName"></v-list-item-title>
                      <v-list-item-subtitle v-text="successApps.version"></v-list-item-subtitle>
                    </v-list-item-content>
                    <v-list-item-icon>
                      <div>
                        <span class="font-weight-medium">
                          <v-chip outlined color="blue">Installed</v-chip>
                        </span>
                      </div>
                    </v-list-item-icon>
                  </v-list-item>
                </template>
              </v-list-item-group>
              <v-list-item-group v-if="installationFailedApps.length > 0">
                <v-subheader> Installation Failed Apps</v-subheader>
                <template v-for="(failedApp, i) in installationFailedApps">
                  <v-row dense v-if="failedApp.isHeader" :key="`installation-failed-header-${i}`">
                    <v-col cols="12" class="pl-3">
                      <span class="caption font-weight-bold">{{ failedApp.name }}</span>
                    </v-col>
                  </v-row>
                  <v-list-item v-if="failedApp.isListItem" :key="`installation-failed-${i}`">
                    <v-list-item-content>
                      <v-list-item-title v-text="failedApp.service"></v-list-item-title>
                      <v-list-item-subtitle v-text="failedApp.version"></v-list-item-subtitle>
                      <v-list-item-subtitle v-text="failedApp.error"></v-list-item-subtitle>
                    </v-list-item-content>
                    <v-list-item-icon>
                      <div>
                        <span class="font-weight-medium">
                          <v-chip outlined color="red">Failed</v-chip>
                        </span>
                      </div>
                    </v-list-item-icon>
                  </v-list-item>
                </template>
              </v-list-item-group>
            </v-list>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-card-text>
    <v-card-actions>
      <v-card-actions>
        <v-btn
          color="primary"
          @click="next"
          :loading="multiStepViewConfig[currentView].loader"
          :disabled="multiStepViewConfig[currentView].disabled"
          v-if="multiStepViewConfig[currentView].hasForward"
        >
          {{
            typeof multiStepViewConfig[currentView].buttonLabel === 'function'
              ? multiStepViewConfig[currentView].buttonLabel(serviceStatus)
              : multiStepViewConfig[currentView].buttonLabel
          }}
        </v-btn>
        <v-btn
          color="pink"
          :disabled="multiStepViewConfig[currentView].disabled"
          v-if="multiStepViewConfig[currentView].hasCancelInstallation"
          @click="cancelInstallation"
          text
        >
          Cancel Install
        </v-btn>

        <v-btn v-if="multiStepViewConfig[currentView].hasCancel" @click="cancel" text> Close </v-btn>
      </v-card-actions>
    </v-card-actions>
  </v-card>
</template>
<script>
import AppStatusChip from '../Services/AppStatusChip';
import _zipObject from 'lodash/zipObject';
import TextFieldDisplay from '../Common/TextFieldDisplay';
import * as slaveAppsService from '../../services/slaveAppsService';

const DOWNLOAD_IN_PROGRESS = 'DOWNLOAD_IN_PROGRESS';
const DOWNLOAD_COMPLETE = 'DOWNLOAD_COMPLETE';

export default {
  props: [
    'selectedApps',
    'slaveDevices',
    'appVersions',
    'clearSelectedApps',
    'closeDialog',
    'fetchDownloadAppState',
    'currentDownloadState',
    'slaveAppsDownloadStates',
    'clearDownloadAppStateInterval',
    'deleteDownloadState',
  ],
  components: {
    AppStatusChip,
    TextFieldDisplay,
  },
  data() {
    return {
      versionForm: false,
      renderReleaseCount: 0,
      successInstalledApps: {},
      failureInstallApps: {},
      currentView: 1,
      refreshTime: 10000,
      versionRules: [value => !!value || 'Version No. is required.'],
      multiStepViewConfig: {
        1: {
          hasBack: false,
          hasForward: true,
          title: 'Selected Apps',
          buttonLabel: 'Continue',
          loader: false,
          disabled: false,
          hasCancel: true,
        },
        2: {
          hasBack: true,
          hasForward: true,
          title: 'Download',
          buttonLabel: 'Download App',
          loader: false,
          disabled: false,
          hasCancel: true,
        },
        3: {
          hasBack: false,
          hasForward: true,
          title: 'Download',
          buttonLabel: 'Install',
          hasCancelInstallation: true,
          loader: false,
          disabled: true,
          hasCancel: true,
          cancelTitle: 'Close',
        },
        4: {
          hasBack: false,
          hasForward: true,
          title: 'Installation Complete',
          buttonLabel: 'Ok',
          loader: false,
          disabled: false,
          hasCancel: false,
        },
      },
    };
  },
  updated() {
    if (!this.hasSelectedApps && !this.currentDownloadState && this.currentDownloadState !== DOWNLOAD_COMPLETE) {
      this.currentView = 1;
      this.multiStepViewConfig[1].disabled = true;
    } else {
      this.multiStepViewConfig[1].disabled = false;
    }

    if (this.currentDownloadState === DOWNLOAD_IN_PROGRESS || !this.currentDownloadState) {
      this.multiStepViewConfig[3].disabled = true;
    } else {
      this.multiStepViewConfig[3].disabled = false;
    }
  },
  watch: {
    currentDownloadState: function (newVal, oldVal) {
      this.showDownloadStateView(newVal);
    },
  },
  created() {
    this.showDownloadStateView(this.currentDownloadState);
  },
  computed: {
    currentBulkAppDownloadState() {
      return this.$store.state.currentBulkAppDownloadState;
    },
    hasSelectedApps() {
      return Object.keys(this.selectedApps).reduce((acc, curr) => {
        const selectedService = this.selectedApps[curr];
        const hasSelectedApps = selectedService && Array.isArray(selectedService) && selectedService.length > 0;
        return acc || hasSelectedApps;
      }, false);
    },
    apps() {
      function transformAppData(selectedApps) {
        return selectedApps.map(app => ({
          ...app,
          isListItem: true,
          serviceName: app.service,
          slaveClientName: app.slave_client_name,
          slaveDeviceName: app.slave_device_name,
          state: app.state,
          version: app.version,
          latestVersion: app.latest_version,
          minVersion: app.min_support_version,
          displayName: app.display_name,
          loadingReleases: false,
          releases: [],
        }));
      }
      const selectedApps = Object.keys(this.selectedApps)
        .reduce((acc, curr) => {
          if (this.selectedApps[curr] && this.selectedApps[curr].length > 0) {
            acc.push(this.getHeader(curr));
            acc.push(...transformAppData(this.selectedApps[curr]));
            acc.push(this.getVersionSelection(curr, this.selectedApps[curr]));
            acc.push({ divider: true, isNonItem: true });
          }
          return acc;
        }, [])
        .filter(val => val);

      return selectedApps;
    },
    downloadStates() {
      const slaveDevices = this.slaveDevices;
      function transformAppData(selectedApps) {
        return selectedApps.map(app => ({
          ...app,
          isListItem: true,
          serviceName: app.service,
          slaveDeviceName: slaveDevices[app.slaveId].device_name,
          version: app.version,
          hasError: !!app.error,
          errorMessage: app.error,
          isDownloading: app.download,
        }));
      }
      const downloadStatesApps = Object.keys(this.slaveAppsDownloadStates).reduce((acc, curr) => {
        if (
          this.slaveAppsDownloadStates[curr] &&
          this.slaveAppsDownloadStates[curr].dataItems &&
          this.slaveAppsDownloadStates[curr].dataItems.length > 0
        ) {
          acc.push(this.getHeader(curr));
          acc.push(...transformAppData(this.slaveAppsDownloadStates[curr].dataItems));
          acc.push({ divider: true, isNonItem: true });
        }
        return acc;
      }, []);
      return downloadStatesApps;
    },
    installationSuccessApps() {
      const slaveDevices = this.slaveDevices;

      function transformAppData(selectedApps) {
        return selectedApps.map(app => ({
          ...app,
          slaveDeviceName: slaveDevices[app.slaveId].device_name,

          isListItem: true,
        }));
      }
      const successInstalledApps = Object.keys(this.successInstalledApps).reduce((acc, curr) => {
        if (
          this.successInstalledApps[curr] &&
          this.successInstalledApps[curr].dataItems &&
          this.successInstalledApps[curr].dataItems.length > 0
        ) {
          acc.push(this.getHeader(curr));
          acc.push(...transformAppData(this.successInstalledApps[curr].dataItems));
          acc.push({ divider: true, isNonItem: true });
        }
        return acc;
      }, []);
      return successInstalledApps;
    },
    installationFailedApps() {
      const slaveDevices = this.slaveDevices;

      function transformAppData(selectedApps) {
        return selectedApps.map(app => ({
          ...app,
          slaveDeviceName: slaveDevices[app.slaveId].site_name,
          isListItem: true,
        }));
      }
      const failureInstallApps = Object.keys(this.failureInstallApps).reduce((acc, curr) => {
        if (
          this.failureInstallApps[curr] &&
          this.failureInstallApps[curr].dataItems &&
          this.failureInstallApps[curr].dataItems.length > 0
        ) {
          acc.push(this.getHeader(curr));
          acc.push(...transformAppData(this.failureInstallApps[curr].dataItems));
          acc.push({ divider: true, isNonItem: true });
        }
        return acc;
      }, []);
      return failureInstallApps;
    },
  },
  methods: {
    getHeader(name) {
      let header = {
        text: name,
        name: name,
        isHeader: true,
        isNonItem: true,
        isListItem: false,
      };
      return header;
    },
    getCommonAppMinVersion(apps) {
      const appVersions = apps.map(app => app.min_support_version);
      const appMinVersionsCount = appVersions.reduce((acc, val) => {
        let count = acc[val] || 0;
        acc[val] = count + 1;
        return acc;
      }, {});
      const appMinVersionsCounts = Object.keys(appMinVersionsCount).map(key => appMinVersionsCount[key]);
      const indexOfCommonVersion = appMinVersionsCounts.indexOf(Math.max(...appMinVersionsCounts));
      return indexOfCommonVersion > -1 ? appVersions[indexOfCommonVersion] : '';
    },
    getVersionSelection(name, selectedApps) {
      let defaultValue = '';

      const appVersions = selectedApps
        .map(app => app.version)
        .filter((curr, index, self) => self.indexOf(curr) === index);
      const commonAppVersion = this.getCommonAppMinVersion(selectedApps);
      if (appVersions.length === 1) {
        defaultValue = appVersions[0];
      } else {
        defaultValue = commonAppVersion;
      }
      return {
        text: name,
        name: name,
        model: defaultValue,
        isListItem: false,
        isVersionSelect: true,
        versions: appVersions,
      };
    },
    back() {
      this.currentView = this.currentView - 1;
    },
    spinLoader(value) {
      this.multiStepViewConfig[this.currentView].loader = value;
    },
    showDownloadStateView(currentDownloadState) {
      switch (currentDownloadState) {
        case DOWNLOAD_IN_PROGRESS:
          this.currentView = 3; // Download Waiting screen
          break;
        case DOWNLOAD_COMPLETE:
          this.currentView = 3; // Show the download report page
          this.clearDownloadAppStateInterval();
          break;
      }
    },
    disableInstallationButton() {
      this.multiStepViewConfig[3].disabled = true;
    },
    bulkDownloadApps() {
      this.currentView = 3;
      this.disableInstallationButton();
      const versionSelectMap = this.apps.reduce((acc, curr) => {
        if (curr.isVersionSelect) {
          acc[curr.name] = curr.model;
        }
        return acc;
      }, {});
      const payload = this.apps
        .filter(item => item.isListItem)
        .map(app => ({ version: versionSelectMap[app.service], service: app.serviceName, slaveId: app.slaveId }));
      this.spinLoader(true);
      const donwloadPromise = slaveAppsService
        .bulkDownloadSlaveApps(payload)
        .then(() => {
          this.spinLoader(false);
          this.fetchDownloadAppState();
        })
        .catch(err => {
          this.spinLoader(false);
        });

      return donwloadPromise;
    },
    async next(step) {
      const currentView = this.currentView;
      switch (currentView) {
        case 1:
          this.stopAndContinue();
          break;
        case 2:
          this.download();
          break;
        case 3:
          this.install();
          break;
        case 4:
          this.continue();
          break;
        default:
          this.currentView++;
          break;
      }
    },
    stopAndContinue() {
      this.currentView++;
    },
    download() {
      if (!this.$refs.versionForm.validate()) {
        return;
      }
      this.bulkDownloadApps();
    },
    install() {
      const installPayload = this.downloadStates
        .filter(item => item.isListItem && !item.hasError)
        .map(app => ({ version: app.version, service: app.serviceName, slaveId: app.slaveId }));
      this.spinLoader(true);
      return slaveAppsService
        .bulkInstallSlaveApps(installPayload)
        .then(({ successInstalledApps = {}, errorInstalledApps = {} }) => {
          this.spinLoader(false);
          this.currentView = 4; // Installation complete
          this.successInstalledApps = { ...successInstalledApps };
          this.failureInstallApps = { ...errorInstalledApps };
        })
        .catch(err => {
          this.spinLoader(false);
          this.$toasted.show('Unexpected error occured while bulk installing apps. Please try again');
        });
    },
    continue() {
      this.resetDialog();
    },
    cancel() {
      this.fetchDownloadAppState();
      this.closeDialog();
    },
    cancelInstallation() {
      this.resetDialog();
    },
    resetDialog() {
      this.versionForm = false;
      this.renderReleaseCount = 0;
      this.currentView = 1;
      this.multiStepViewConfig = {
        1: {
          hasBack: false,
          hasForward: true,
          title: 'Selected Apps',
          buttonLabel: 'Continue',
          loader: false,
          disabled: false,
          hasCancel: true,
        },
        2: {
          hasBack: true,
          hasForward: true,
          title: 'Download',
          buttonLabel: 'Download App',
          loader: false,
          disabled: false,
          hasCancel: true,
        },
        3: {
          hasBack: false,
          hasForward: true,
          title: 'Download',
          buttonLabel: 'Install',
          hasCancelInstallation: true,
          loader: false,
          disabled: true,
          hasCancel: true,
          cancelTitle: 'Close',
        },
        4: {
          hasBack: false,
          hasForward: true,
          title: 'Installation Complete',
          buttonLabel: 'Ok',
          loader: false,
          disabled: false,
          hasCancel: false,
        },
      };
      this.deleteDownloadState();
      this.clearSelectedApps();
      this.closeDialog(true, { unmount: true });
    },
  },
};
</script>
