<template>
  <v-card :loading="isLoadingSlaveApps || isFetchingAppData">
    <v-toolbar flat>
      <v-toolbar-title>Slave Apps</v-toolbar-title>
      <v-text-field
        v-model="searchText"
        class="pl-10"
        append-icon="mdi-magnify"
        label="Search using site or device name"
        single-line
        hide-details
      ></v-text-field>
      <v-menu bottom left>
        <template v-slot:activator="{ on, attrs }">
          <v-btn dark icon v-bind="attrs" v-on="on">
            <v-icon :color="appliedFilterCount > 0 ? 'blue' : 'white'">mdi-filter</v-icon>
          </v-btn>
        </template>

        <v-list dense>
          <v-subheader>Filters</v-subheader>

          <v-list-item>
            <v-list-item-title>
              <v-checkbox class="mt-0" hide-details v-model="filters.isInstalled" label="Installed Apps"></v-checkbox>
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>

      <v-spacer></v-spacer>
      <v-btn
        v-if="!!currentDownloadState"
        @click="() => (isClearDialog = true)"
        x-medium
        color="default"
        class="white--text ml-3"
      >
        <span>Clear Update</span>
        <v-icon x-medium right dark> mdi-cellphone-erase </v-icon>
      </v-btn>
      <v-btn @click="onBulkUpdate" x-medium color="pink" class="white--text">
        <v-progress-circular
          v-if="showDownloadProgressIcon"
          :width="2"
          :size="15"
          color="white"
          indeterminate
        ></v-progress-circular>
        <v-icon v-if="showCheckDownloadIcon" x-medium dark> mdi-check</v-icon>
        <span class="pl-2">Bulk Update</span>
        <v-icon x-medium right dark> mdi-cellphone-arrow-down </v-icon>
      </v-btn>
    </v-toolbar>
    <v-spacer></v-spacer>
    <v-card-text>
      <v-container fluid v-if="!isLoadingSlaveApps">
        <v-row no-gutter>
          <v-col cols="12" lg="6" md="6" sm="12" xs="12" v-for="slaveApps in slaveAppsDetail" :key="slaveApps.name">
            <slave-app-grid
              :slave-devices="slaveDevices"
              :search-text="searchText"
              :filters="filters"
              :app-versions="selectedAppVersion"
              :onSelectItem="onSelectItem"
              :key="`${recompute}-${slaveApps.name}`"
              :selected-item="selectedItems[slaveApps.name]"
              :slave-apps="slaveApps"
            ></slave-app-grid>
          </v-col>
        </v-row>
      </v-container>
      <v-col cols="12" v-if="isLoadingSlaveApps">
        <v-skeleton-loader type="table-heading, list-item-two-line, image, table-tfoot"></v-skeleton-loader>
      </v-col>
      <v-col cols="12" class="text-left" v-if="isFetchAppFailiure">
        <v-alert v-if="fetchErrorMessage" class="mb-0 text-left" dense outlined type="error">
          {{ fetchErrorMessage }}
        </v-alert>
        <v-btn color="primary" class="mt-10" @click="retryFetchApps">Click here to retry</v-btn>
      </v-col>
    </v-card-text>
    <v-dialog persistent v-model="dialog[dialogNames.bulkUpdate]" max-width="600px">
      <slave-apps-download-multi-step
        :app-versions="selectedAppVersion"
        :slave-devices="slaveDevices"
        :close-dialog="() => closeDialog(dialogNames.bulkUpdate)"
        :selected-apps="selectedItems"
        :clear-selected-apps="clearSelectedApps"
        :delete-download-state="deleteDownloadState"
        :current-download-state="currentDownloadState"
        :slave-apps-download-states="slaveAppsDownloadStates"
        :fetch-download-app-state="fetchDownloadAppState"
        :clear-download-app-state-interval="clearDownloadAppStateInterval"
      ></slave-apps-download-multi-step>
    </v-dialog>
    <confirm-dialog
      :is-open="isClearDialog"
      :on-close="() => (isClearDialog = false)"
      :on-confirm="onClearDownload"
      dialog-text="All progress will be lost. Are you sure?"
    ></confirm-dialog>
  </v-card>
</template>
<script>
import PageTitle from '../Common/PageTitle';
import SlaveAppGrid from './SlaveAppGrid';
import ConfirmDialog from '../Common/ConfirmDialog';
import * as slaveAppsUiService from '../../services/slaveAppsUiService';
import SlaveAppsDownloadMultiStep from './SlaveAppsDownloadMultiStep';
import * as slaveAppsService from '../../services/slaveAppsService';
import * as slaveDeviceService from '../../services/slaveDevicesService';
import { APP_DOWNLOAD_STATUSES } from '../../constants/appConstant';

export default {
  name: 'slave-apps',
  components: { PageTitle, SlaveAppGrid, SlaveAppsDownloadMultiStep, ConfirmDialog },
  data() {
    const dialogNames = {
      bulkUpdate: 'bulkUpdate',
    };
    return {
      recompute: 0,
      filters: {
        isInstalled: false,
      },
      isFetchAppFailiure: false,
      isClearDialog: false,
      isLoadingSlaveApps: false,
      slaveReleaseList: null,
      fetchErrorMessage: '',
      searchText: '',
      isFetchingAppData: false,
      refreshTime: 5000,
      isDeletingDownloadState: false,
      currentDownloadState: null,
      slaveAppsDetailMap: {},
      slaveAppsDetail: [],
      selectedItems: {},
      appVersions: {},
      selectedAppVersion: {},
      slaveAppReleases: {},
      fetchDownloadAppStateInterval: null,
      slaveDevices: {},
      slaveAppsDownloadStates: {},
      dialogNames,
      dialog: {
        [dialogNames.bulkUpdate]: false,
      },
      remountDialog: {
        [dialogNames.bulkUpdate]: true,
      },
    };
  },
  mounted() {
    this.initialize();
  },
  computed: {
    showCheckDownloadIcon() {
      return this.currentDownloadState === APP_DOWNLOAD_STATUSES.DOWNLOAD_COMPLETE;
    },
    showDownloadProgressIcon() {
      return this.currentDownloadState === APP_DOWNLOAD_STATUSES.DOWNLOAD_IN_PROGRESS;
    },
    appliedFilterCount() {
      return Object.keys(this.filters).reduce((acc, filterKey) => {
        if (this.filters[filterKey]) {
          acc++;
        }
        return acc;
      }, 0);
    },
  },
  methods: {
    retryFetchApps() {
      this.fetchErrorMessage = '';
      this.isFetchAppFailiure = false;
      this.initialize();
    },
    async initialize() {
      this.isFetchingAppData = true;
      await Promise.all([this.fetchSlaveApps(true), this.fetchSlaveDevices()]).finally(() => {
        this.isFetchingAppData = false;
        this.recompute++;
      });
    },
    openDialog(dialogName) {
      this.dialog[dialogName] = true;
    },
    toggleDialog(dialogName, item) {
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedApp = item;
      } else {
        this.selectedApp = null;
      }
    },
    fetchSlaveApps(fetchAppReleases = false) {
      this.isLoadingSlaveApps = true;
      return slaveAppsService
        .fetch()
        .then(({ data = {} }) => {
          this.slaveAppsDetailMap = data;

          this.slaveAppsDetail = Object.keys(data).map(slaveAppKey => data[slaveAppKey]);
          this.selectedItems = Object.keys(data).reduce((acc, curr) => {
            acc[curr] = [];
            return acc;
          }, {});
          if (fetchAppReleases) {
            this.fetchSlaveAppReleases();
          }
        })
        .catch(err => {
          if (err && err.message) {
            this.$toasted.show(err.message, { type: 'error' });
            this.fetchErrorMessage = err.message;
          }
          this.isFetchAppFailiure = true;
        })
        .finally(() => {
          this.isLoadingSlaveApps = false;
        });
    },
    onSelectItem(serviceName, items) {
      this.selectedItems[serviceName] = items;
      // Recompute here
      this.selectedAppVersion = slaveAppsUiService.slaveReleaseList({
        releaseMap: this.appVersions,
        selectedItems: this.selectedItems,
      });
    },
    fetchSlaveDevices() {
      return slaveDeviceService
        .fetch()
        .then(slaveDevices => {
          this.slaveDevices = slaveDevices;
          this.fetchDownloadAppState();
        })
        .catch(err => {
          this.$toasted.show('Error fetching slave devies');
        });
    },
    fetchSlaveAppReleases() {
      const serviceNames = Object.keys(this.slaveAppsDetailMap);

      if (!serviceNames.length) {
        return null;
      }

      return slaveAppsService
        .fetchAppsReleases(serviceNames)
        .then(appVersions => {
          this.appVersions = serviceNames.reduce((acc, serviceName, index) => {
            acc[serviceName] = appVersions[index];
            return acc;
          }, {});
        })
        .catch(err => {
          this.$toasted.show('Error fetching app releases');
        });
    },
    onBulkUpdate() {
      this.dialog[this.dialogNames.bulkUpdate] = true;
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
    },
    startDownloadStateInterval() {
      if (!this.fetchDownloadAppStateInterval) {
        this.fetchDownloadAppStateInterval = setInterval(() => {
          this.fetchDownloadAppState();
        }, this.refreshTime);
      }
    },
    clearDownloadAppStateInterval() {
      clearInterval(this.fetchDownloadAppStateInterval);
      this.fetchDownloadAppStateInterval = null;
    },
    deleteDownloadState() {
      this.isDeletingDownloadState = true;
      const slaveUUIDs = Object.keys(this.slaveDevices);
      if (!(slaveUUIDs && slaveUUIDs.length > 0)) {
        return null;
      }
      return slaveAppsService.clearDownloadState(slaveUUIDs).finally(() => {
        this.fetchDownloadAppState();
        this.clearDownloadAppStateInterval();
        this.isDeletingDownloadState = false;
      });
    },
    fetchDownloadAppState() {
      const slaveUUIDs = Object.keys(this.slaveDevices);
      if (!(slaveUUIDs && slaveUUIDs.length > 0)) {
        return null;
      }
      return slaveAppsService
        .fetchDownloadState(slaveUUIDs)
        .then(({ data: slaveAppsDownloadStates, globalState: downloadState }) => {
          switch (downloadState) {
            case 'DOWNLOADING':
              this.currentDownloadState = APP_DOWNLOAD_STATUSES.DOWNLOAD_IN_PROGRESS;
              // this.isBulkDownloadAppPersistent = false;
              if (!this.fetchDownloadAppStateInterval) {
                this.startDownloadStateInterval();
              }
              break;
            case 'CLEARED':
              this.currentDownloadState = null;
              this.clearDownloadAppStateInterval();
              break;
            case 'DOWNLOADED':
              this.currentDownloadState = APP_DOWNLOAD_STATUSES.DOWNLOAD_COMPLETE;
              // this.isBulkDownloadAppPersistent = true;
              this.clearDownloadAppStateInterval();
              break;
          }
          this.slaveAppsDownloadStates = slaveAppsDownloadStates;
          Object.keys(this.slaveAppsDownloadStates).forEach(serviceName => {
            this.slaveAppsDownloadStates[serviceName].dataItems = this.slaveAppsDownloadStates[
              serviceName
            ].dataItems.map(data => this.appendSlaveDevice(data));
          });
          return slaveAppsDownloadStates;
        })
        .catch(err => {});
    },
    clearSelectedApps() {
      this.slaveAppsDownloadStates = {};
      this.selectedItems = {};
      this.recompute++;
      this.fetchSlaveApps();
    },
    appendSlaveDevice(data) {
      let newData = { ...data };
      const slaveDevice = this.slaveDevices[data.slaveId];
      if (slaveDevice) {
        newData = {
          ...newData,
          ...Object.keys(slaveDevice).reduce((acc, curr) => {
            const key = 'slave_' + curr;
            acc[key] = slaveDevice[curr];
            return acc;
          }, {}),
        };
      }
      return newData;
    },
    onClearDownload() {
      this.clearSelectedApps();
      this.deleteDownloadState().finally(() => {
        this.isClearDialog = false;
      });
    },
  },
};
</script>
