<template>
  <div>
    <page-title title="Rubix Apps"></page-title>
    <v-row v-if="isMaster" align="center" justify="center">
      <v-btn-toggle  v-model="toggleMasterSlave" rounded>
        <v-btn small :value="1"> Master View </v-btn>
        <v-btn small :value="2"> Slave View </v-btn>
      </v-btn-toggle>
    </v-row>
    <v-row>
      <v-col cols="12" v-if="!isMqttRestBridgeConfigLoaded">
        <v-skeleton-loader type="table-heading, list-item-two-line, image, table-tfoot"></v-skeleton-loader>
      </v-col>
      <v-col cols="12" v-if="isMqttRestBridgeConfigLoaded && toggleMasterSlave == 2">
        <apps-grid></apps-grid>
      </v-col>
      <v-col cols="12" v-if="isMqttRestBridgeConfigLoaded && toggleMasterSlave == 1">
        <slave-apps></slave-apps>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import AppsGrid from './AppsGrid';
import SlaveApps from './SlaveApps';
import PageTitle from '../Common/PageTitle.vue';
const MASTER_VIEW = 1;
const SLAVE_VIEW = 2;
export default {
  name: 'rubix-apps',
  data() {
    return {
      toggleMasterSlave: MASTER_VIEW,
    };
  },
  components: {
    AppsGrid,
    PageTitle,
    SlaveApps,
  },
  computed: {
    isMqttRestBridgeConfigLoaded() {
      return Object.keys(this.$store.state.mqttRestBridgeConfig || {}).length > 0;
    },
    isMaster() {
      return this.$store.state.isMaster;
    },
  },
  watch: {
    isMaster: function (isMaster) {
      if (isMaster) {
        this.toggleMasterSlave = MASTER_VIEW;
      } else {
        this.toggleMasterSlave = SLAVE_VIEW;
      }
    },
  },
};
</script>
